import Bmob from "hydrogen-js-sdk"
export default {
	data() {
		return {
			isCard: true,
			blogList:[],
			isLazyLoad:true,
			objectId:''
		}
	},
	onLoad() {
		this.loadUserData();
		this.initBlogList();
	},
	onShow() {
		
	},
	methods: {
		loadUserData(){//获取用户信息
			var THAT = this;
			let current = Bmob.User.current();//获取登陆用户信息
			if(!current){
				uni.reLaunch({
					url:'../login/login'
				})
			}
			else{
				THAT.objectId = current.objectId;
			}
		},
		// 大图预览 + 保存到相册
		previewImg(list,index){
			uni.previewImage({
				urls:list,
				current:index,
				longPressActions:{
					itemList:['保存到相册'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			})
		},
		// 初始化首页数据
		initBlogList(){
			var THAT = this;
			// 查表MicroBlog
			var query = Bmob.Query('MicroBlog');
			// 查询详细creator
			query.include('creator');
			// 按创建时间倒序
			query.order("-createdAt");
			// 一次加载5条
			query.limit(5);
			query.find().then(res => {
				var _THAT = THAT
				// 数据处理
				res.map((currentValue,index,arr)=>{
					currentValue.isLike = false
					// 分割imgList
					if(res.imgList!=''){
						currentValue.imgList=currentValue.imgList.split(",");
					}
					// 查询点赞的关联关系
					let queryLike = Bmob.Query('MicroBlog')
					queryLike.field('likes',currentValue.objectId)
					// 查询点赞的具体的用户信息
					queryLike.relation('Like').then(result => {
					  console.log(index,result);
					  currentValue.likes = result;
					  // 查询是否点赞这条帖子
					  result.results.map(cur => {
						  console.log(cur,_THAT.objectId)
						  if(cur.creator.objectId == _THAT.objectId){
							  currentValue.isLike = true
						  }
					  })
					})
				})
				// 数据赋值
				setTimeout(()=>{
					THAT.blogList=res;
					console.log(THAT.blogList)
				},0)
			}).catch(err => {
				console.log(err)
			})
		},
		// 点赞
		like(itemid,index,creatorid){
			// 关联点赞者
			const pointer = Bmob.Pointer('_User')
			const poiID = pointer.set(this.objectId)
			// 关联被点赞帖子
			const pointer_blog = Bmob.Pointer('MicroBlog')
			const poiID_blog = pointer.set(itemid)
			// 关联被点赞者
			const pointer_creator = Bmob.Pointer('_User')
			const poiID_creator = pointer.set(creatorid)
			// 写入数据库Like表 写入点赞记录
			const query = Bmob.Query('Like')
			query.set('creator',poiID)
			query.set('liked_blog',poiID_blog)
			query.set('liked_creator',poiID_creator)
			query.save().then(res => {
				console.log(res)
				const relation = Bmob.Relation('Like') // 需要关联的表
				const relID = relation.add([res.objectId]) //关联表中需要关联的objectId, 返回一个Relation对象, add方法接受string和array的类型参数
				const query1 = Bmob.Query('MicroBlog')
				query1.get(itemid).then(result => {
					result.set('likes',relID); // 将Relation对象保存到likes字段中，即实现了一对多的关联
					result.save()
					console.log(result);
				})
			}).catch(err => {
				console.log(err)
			})
			this.blogList[index].isLike = true
			this.blogList[index].likes.count++
		},
		// 取消点赞
		unlike(itemid,index){
			// 查询符合条件的点赞表的记录
			const query = Bmob.Query('Like');
			query.equalTo("creator", '===', this.objectId);
			query.equalTo("liked_blog", '===', itemid);
			query.find().then(res => {
				console.log(res)
				const query = Bmob.Query('Like');
				query.destroy(res[0].objectId).then(result => {
					console.log(result)
				}).catch(err => {
					console.log(err)
				})
			})
			this.blogList[index].isLike = false
			this.blogList[index].likes.count--
		}
	}
}
